<template>
    <div class="novice" ref="novice" v-if="novice < 2">
        <img class="novice-images image1" src="../../../static/image/novice/guide1.png" v-if="novice === 0"/>
        <img class="novice-images image2" src="../../../static/image/novice/guide2.png" v-if="novice === 1"/>
        <img class="novice-images image3" src="../../../static/image/novice/guide3.png" v-if="novice === 0"/>
        <img class="novice-images image4" src="../../../static/image/novice/guide4.png" v-if="novice === 1"/>
        <img class="novice-images image5" src="../../../static/image/novice/guide5.png" v-if="novice === 0"/>
        <img class="novice-images image6" src="../../../static/image/novice/guide6.png" v-if="novice === 1"/>

        <div class="novice-button-wrapper">
            <div class="button" @click="nextStep">
                {{this.novice === 0 ? '' : ''}}
            </div>
        </div>
    </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex'

    export default {
        name: "novice",
        computed: {
            ...mapGetters({
                novice: 'novice/novice'
            })
        },
        methods: {
            ...mapActions({
                nextStep: 'novice/nextStep'
            })
        }
    }
</script>

<style lang="stylus" scoped>
    .novice
        position fixed
        top 0
        left 0
        right 0
        bottom 0
        z-index 1001
        background-color rgba(0, 0, 0, .7)
        .novice-images
            position absolute
            max-width 38%
            &.image1
                left 40px
                top 75px
            &.image2
                right 0
                top 110px
            &.image3
                right 25px
                top 175px
            &.image4
                left 20px
                top 115px
            &.image5
                left 60px
                bottom 90px
            &.image6
                right 50px
                bottom 90px
        .novice-button-wrapper
            position absolute
            top 50%
            left 50%
            transform translate3d(-50%, -50%, 0)
            .button
                padding 10px 18px
                color #fff
                border 1px solid #fff
                border-radius 7px
</style>



