<template>
    <div class="map-wrapper" data-enable="true">
        <header class="map-search-wrapper shadow" :class="{__novice: novice === 0}">
            <mu-avatar size="30" @click.native="clickUserAvatar">
                <img v-lazy="AvatarImage($images + user.portrait)">
            </mu-avatar>

            <div class="search-box_inner_text no-wrap" @click="clickSearchBox"> </div>
        </header>

        <!-- map-button -->
        <!--  -->
        <div class="shadow map-safety-button"
             :class="{__element_phone: elementPanelVisible}"
             @click="safety.visible = true">
            <img src="../../static/icon/map_sos.png">
            <div>
                <span></span>
                <mu-icon value="chevron_right"/>
            </div>
        </div>
        <location-button class="map-button shadow map-location-button"
                         :class="{__novice: novice === 0, __element: elementPanelVisible}"/>
        <!--  -->
        <selection-button class="map-button shadow map-selection-button"
                          :class="{__novice: novice === 0}"
                          :selection="selection"
                          @click="handlerMapSelection"/>
        <!--  -->
        <!--<selection-button class="map-button shadow map-report-button"-->
        <!--:selection="report"-->
        <!--@click="handlerMapReport"/>-->
        <!--  -->
        <address-button class="map-button shadow map-address-button"
                        :class="{__novice: novice === 1, __element: elementPanelVisible}"/>
        <!--  -->
        <message-button class="map-button shadow map-message-button"
                        :class="{__novice: novice === 1}"/>
        <!--  -->
        <layer-button class="map-button shadow map-layer-button"
                      :class="{__novice: novice === 1}"
                      :disabled="novice === 1"/>
        <!--  -->
        <compass-button class="map-button shadow map-compass-button"
                        @click="handlerCompassTransfer(false)"
                        v-if="angle === ANGLE_HEAD.value"/>
        <!--  -->
        <depth-button class="map-depth-button"
                      :class="{__element_depth: elementPanelVisible}"
                      v-if="depth"/>
        <!-- map-button -->

        <!--  -->
        <footer class="map-navigate-list border-1px-t shadow">
            <mu-ripple class="navigate-list-item" @click.stop="handlerNavigation">
                <img src="../../static/icon/map_route.png">
                <span></span>
            </mu-ripple>
            <i class="line border-1px-l"></i>
            <mu-ripple class="navigate-list-item" @click.stop="handlerNavigational">
                <img src="../../static/icon/map_navigation_aid.png">
                <span></span>
            </mu-ripple>
            <i class="line border-1px-l"></i>
            <mu-ripple class="navigate-list-item" @click.stop="handlerNews">
                <img src="../../static/icon/map_news.png">
                <span></span>
            </mu-ripple>
            <i class="line border-1px-l"></i>
            <mu-ripple class="navigate-list-item" @click.stop="handlerDynamic">
                <img src="../../static/icon/map_dynamic.png">
                <span></span>
            </mu-ripple>
        </footer>
        <!--  -->

        <!--  -->
        <center-button v-if="selection || report" @confirm="confirm" @cancel="cancel"/>
        <!--  -->

        <!-- &&& -->
        <mu-dialog title="" max-width="90%" width="500px" scrollable :open.sync="ships.visible">
            <mu-form :model="ships.action" label-position="left" label-width="100">
                <mu-form-item prop="name" label="">
                    <mu-select v-model="ships.action.name" @change="changeShip" solo>
                        <mu-option v-for="(item, index) in ships.list"
                                   :key="index"
                                   :label="item.name"
                                   :value="item.name"/>
                    </mu-select>
                </mu-form-item>
                <mu-form-item prop="draft" label="">
                    <mu-text-field v-model="ships.action.draft"
                                   placeholder=""
                                   suffix="" type="number"
                                   solo/>
                </mu-form-item>
                <mu-form-item prop="speed" label="">
                    <mu-text-field v-model="ships.action.speed"
                                   placeholder=""
                                   suffix="km/h" type="number"
                                   solo/>
                </mu-form-item>
                <mu-form-item prop="pureHeight" label="">
                    <mu-text-field v-model="ships.action.customHeight"
                                   placeholder=""
                                   suffix="" type="number"
                                   solo/>
                </mu-form-item>
            </mu-form>

            <mu-button slot="actions" @click="ships.visible = false" flat></mu-button>
            <mu-button slot="actions" @click="handlerNavigation" color="primary" flat></mu-button>
        </mu-dialog>
        <!-- dialog -->

        <!--  -->
        <transition name="views-up">
            <div class="element-panel-wrapper" v-if="elementPanelVisible">
                <element-panel @route="elementPanelRoute" ref="panel"/>
            </div>
        </transition>
        <!--  -->

        <!--  -->
        <popup class="safety-popup" v-model="safety.visible" position="bottom">
            <h3 class="border-1px-b"></h3>

            <div class="phones-wrapper">
                <div class="phone" v-for="item in phones" @click="callPhone(item)">
                    <mu-avatar class="icon" color="#ff9800">
                        <span style="font-size: 14px">{{item.typeId}}</span>
                    </mu-avatar>
                    <span>{{item.value}}</span>
                </div>
            </div>

            <mu-icon class="close" @click="safety.visible = false" value="close"/>
        </popup>
        <!--  -->

        <!-- & -->
        <mu-dialog title="" width="500" max-width="88%" :open.sync="safety.call">
            <div style="display: flex; align-items: flex-end">
                <mu-icon value="priority_high" color="#fdd835"/>
                <div style="display: flex; align-items: center; height: 24px">
                    
                    <span style="color: #ff9800; font-size: 1.3em; margin: 0 0.3em">{{safety.phone}}</span>
                    
                </div>
            </div>
            <mu-button slot="actions" color="#333" @click="safety.call = false" flat></mu-button>
            <mu-button slot="actions" color="primary" @click="callPhone()" flat></mu-button>
        </mu-dialog>
        <!-- & -->

        <!--  -->
        <novice/>
        <!-- novice over -->

        <!-- Router -->
        <transition :name="fade">
            <keep-alive>
                <router-view class="views"/>
            </keep-alive>
        </transition>
        <!-- Router -->
    </div>
</template>

<script>
    /* controller */
    import LocationButton from '../components/map/map-location-button/map-location-button'
    import LayerButton from '../components/map/map-layer-button/map-layer-button'
    import SelectionButton from '../components/map/map-selection-button/map-selection-button'
    import AddressButton from '../components/map/map-address-button/map-address-button'
    import MessageButton from '../components/map/map-message-button/map-message-button'
    import CompassButton from '../components/map/map-compass-button/map-compass-button'
    import DepthButton from '../components/map/map-depth-button/map-depth-button'
    import CenterButton from '../components/map/map-center-button/map-center-button'
    /* controller */
    import ElementPanel from '../components/element-panel/element-panel'
    import Novice from '../components/novice/novice'
    import Popup from 'vant/lib/popup'
    import 'vant/lib/popup/style'
    import {mapGetters} from 'vuex'
    import {userMixins, mapMixins, elementMixins, appMixins} from "../common/mixins"
    import {MapMixins} from "../common/js/mixins/index"
    import {queryIShip} from "../api/personal"

    const MAP = 'MAP'


    export default {
        name: MAP,
        mixins: [MapMixins, appMixins, userMixins, mapMixins, elementMixins],
        data() {
            return {
                fade: 'views-left',
                selection: false,
                report: false,
                ships: {
                    visible: false,
                    list: [],
                    action: {
                        name: '',
                        draft: 0,
                        speed: 0,
                        customHeight: 0
                    }
                },
                elementPanelVisible: false,
                element: {
                    id: ''
                },
                transfer: false,
                safety: {
                    visible: false,
                    call: false,
                    phone: ''
                }
            }
        },
        computed: {
            ...mapGetters({
                novice: 'novice/novice'
            })
        },
        created() {
            window.$types.map.element.click = this.handlerElementClick
            window.$types.map.element.close = this.handlerElementClose
            window.$types.map.compass.transfer = this.handlerCompassTransfer
        },
        methods: {
            /***
             * 
             */
            callPhone(item) {
                if (!item) {
                    window.Arcgis && window.Arcgis.callPhone(this.safety.phone)

                    this.safety.call = false

                    return
                }

                this.safety.call = true

                this.safety.phone = item.typeId
            },
            /***
             *   
             */
            clickUserAvatar() {
                if (this.novice === 0) return

                this.fade = 'views-left'

                this.$router.push({name: 'about'})
            },
            /***
             *   
             */
            clickSearchBox() {
                if (this.novice === 0) return

                this.fade = ''

                this.$router.push({name: 'search'})
            },
            /***
             * 
             */
            handlerMapSelection() {
                if (!this.isLogin) {
                    this.$toast('')

                    return
                }

                this.selection = !this.selection

                this.report = false
            },
            handlerMapReport() {
                this.report = !this.report

                this.selection = false
            },
            /***
             * 
             */
            confirm() {
                this.$toast({
                    type: 'loading',
                    message: ' ...',
                    position: 'center',
                    duration: 5000
                })

                this.getCenter().then(res => {
                    this.$toast.clear()

                    try {
                        res = JSON.parse(res)
                    } catch (e) {
                        console.log(e)

                        res = {x: 0, y: 0}

                        this.$toast('')
                    }

                    if (this.selection) {
                        this.$router.push({
                            name: 'selection-point',
                            params: {
                                latitude: res.y,
                                longitude: res.x
                            }
                        })
                    }

                    if (this.report) {
                        this.$router.push({
                            name: 'selection-report',
                            params: {
                                latitude: res.y,
                                longitude: res.x
                            }
                        })
                    }


                    this.selection = false
                    this.report = false
                }).catch(err => {
                    this.$toast('')
                })
            },
            /***
             * 
             */
            cancel() {
                this.report = false
                this.selection = false
            },
            /***
             *  
             */
            handlerNavigation() {
                // 
                if (!this.isLogin) {
                    this.$router.push({
                        name: 'navigation',
                        query: {
                            draft: 0,
                            speed: 0,
                            pureHeight: 0
                        }
                    })

                    return
                }

                // 
                if (this.ships.visible === true) {
                    this.ships.visible = false

                    this.$router.push({
                        name: 'navigation',
                        query: {
                            draft: Number(this.ships.action.draft),
                            speed: Number(this.ships.action.speed),
                            pureHeight: Number(this.ships.action.customHeight)
                        }
                    })

                    return
                }

                // 
                this.ships.action = {
                    name: '',
                        draft: 0,
                        speed: '',
                        pureHeight: 0
                }
                queryIShip({pageNumber: 0, pageSize: 20}).then(res => {
                    this.ships.list = res.content

                    const defaultItem = this.ships.list.find(item => {
                        return item.isDefault === '1' || item.isDefault === 1
                    })

                    Object.assign(this.ships.action, defaultItem || {})

                    this.ships.visible = true
                })
            },
            /***
             * 
             */
            changeShip(value) {
                const item = this.ships.list.find(item => item.name === value)

                Object.assign(this.ships.action, item || {})
            },
            /***
             * 
             */
            handlerNavigational() {
                this.fade = 'views-left'

                this.$router.push({
                    name: 'navigational'
                })
            },
            /***
             *  
             */
            handlerNews() {
                this.fade = 'views-left'

                this.$router.push({
                    name: 'news'
                })
            },
            /***
             *  
             */
            handlerDynamic() {
                if (this.isLogin) {
                    this.fade = 'views-left'

                    this.$router.push({
                        name: 'dynamic'
                    })
                    return
                }

                this.$toast('')
            },
            /***
             * 
             * @param typeId
             * @param data
             * @param type
             */
            handlerElementClick(typeId, data, type) {
                try {
                    data = JSON.parse(data)
                } catch (e) {
                    console.log(e)
                    data = {}
                }

                this.setElementData(data)

                this.elementPanelVisible = true
            },
            /***
             * 
             */
            handlerElementClose() {
                this.elementPanelVisible = false

                this.setElementData({})
            },
            /***
             * 
             * @param data
             */
            elementPanelRoute(data) {
                this.$router.push({
                    name: 'details',
                    params: {
                        id: data.id,
                        type: data.typeid || data.typeId
                    },
                    query: {
                        typeId: data.typeId
                    }
                })
            },
            /***
             * 
             * @param value
             */
            handlerCompassTransfer(value) {
                this.transfer = value
            }
        },
        components: {
            Popup,
            Novice,
            LocationButton,
            LayerButton,
            SelectionButton,
            AddressButton,
            MessageButton,
            CompassButton,
            DepthButton,
            CenterButton,
            ElementPanel
        }
    }
</script>

<style lang="stylus">
    .__novice
        z-index 10001 !important

    .__element
        bottom 120px !important

    .__element_depth
        bottom 210px !important

    .__element_phone
        bottom 160px !important

    .map-wrapper
        width 100%
        height 100%
        overflow hidden
        > .map-search-wrapper
            position absolute
            top 0
            left 0
            right 0
            z-index 1
            display flex
            align-items center
            margin 10px 13px
            padding 0 10px
            height 50px
            background-color #fff
            border-radius 5px
            opacity 0.9
            overflow hidden
            .search-box_inner_text
                padding-left 15px
                flex 1
                height 30px
                line-height 30px
                font-size 14px
                cursor text
                -webkit-rtl-ordering logical
                -webkit-appearance textfield
        > .map-navigate-list
            position absolute
            bottom 0
            left 0
            right 0
            display flex
            align-items center
            height 50px
            background-color rgba(255, 255, 255, .9)
            overflow hidden
            .navigate-list-item
                position relative
                display flex
                align-items center
                justify-content center
                flex 1
                padding 0 10px
                height 100%
                img
                    display block
                    padding 3px
                    width 25px
                    height 25px
                span
                    padding 3px
                    color #4C4C4C
                    font-size 14px
        .map-safety-button
            position absolute
            bottom 110px
            left 13px
            height 30px
            display flex
            align-items center
            opacity 0.9
            transition all .5s
            > img
                position absolute
                top 50%
                left 0
                display block
                padding 5px
                width 30px
                height 30px
                border-radius 50%
                background-color #fff
                transform translate3d(0, -50%, 0)
            > div
                display flex
                align-items center
                margin-left 15px
                padding-left 20px
                height 26px
                font-size 14px
                background-color #fff
                border-radius 0 5px 5px 0
        > .map-button
            &.map-location-button
                bottom 70px
                left 13px
            &.map-layer-button
                top 80px
                right 13px
            &.map-selection-button
                top 130px
                right 13px
            &.map-report-button
                top 170px
                right 13px
            &.map-address-button
                bottom 70px
                right 13px
            &.map-message-button
                top 80px
                left 13px
            &.map-compass-button
                top 130px
                left 13px

        > .map-center-icon
            position absolute
            top 50%
            left 50%
            z-index 999
            transform translate3d(-50%, -90%, 0)
        > .element-panel-wrapper
            position absolute
            bottom 0
            left 0
            right 0
            z-index 999
            background-color rgba(255, 255, 255, .9)

    .safety-popup
        max-height 80%
        h3
            padding 30px 20px 15px
            font-weight 600
        .close
            position absolute
            top 5px
            right 5px
        .phones-wrapper
            display flex
            flex-wrap wrap
            padding 15px 20px
            .phone
                display flex
                align-items center
                flex-direction column
                margin-bottom 15px
                width 33.33333%
                .icon
                    margin-bottom 8px
                span
                    font-size 14px
</style>



