<template>
    <div class="map-depth" data-enable="true" ref="wrapper"
         @touchstart.stop.prevent
         @touchmove.stop.prevent
         @touchend.stop.prevent>
        <img class="depth" src='../../../../static/image/map_ruler.png' alt="">
        <div class="map-depth-group" ref="depth"
             :style="{transform: `translate3d(0, ${translateY}px, 0)`}"
             @touchstart.stop="onTouchStart"
             @touchmove.stop="onTouchMove"
             @touchend.stop="onTouchEnd">
            <div class="map-depth-value">{{depth}}m</div>
        </div>
    </div>
</template>

<script>
    import {MapMixins} from "../../../common/js/mixins"
    import {setMapEnable} from "../../../common/js/cordova/map"
    import {getDepth} from "../../../api/map"
    import Toast from 'muse-ui-toast'

    export default {
        name: "map-depth-button",
        mixins: [MapMixins],
        data() {
            return {
                img: "../../../../static/image/map_ruler.png",
                max: 12,
                min: 3,
                startY: 0,
                currentY: 0,
                translateY: 0,
                percent: 0
            };
        },
        computed: {
            depth() {
                return Math.round(((this.max - this.min) * this.percent + this.min) * 10) / 10
            }
        },
        created() {
            window.Arcgis && window.Arcgis.centerAt(113.596, 22.92, res => {
                window.Arcgis && window.Arcgis.setScale(20000)

                Toast.message({
                    position: 'bottom',
                    message: '',
                    time: 5000
                })

                this.onTouchEnd({})
            }, err => {
                console.log(' ...')
            }, true)
        },
        methods: {
            onTouchStart(event) {
                this.$emit("start", event)

                this.startY = event.targetTouches[0].pageY
                this.currentY = this.startY

                setMapEnable(false)
            },
            onTouchMove(event) {
                const move = event.targetTouches[0].pageY - this.currentY
                const height = this.$refs.wrapper.offsetHeight
                const rangeMax = -height + (this.$refs.depth.offsetHeight / 2) - 1
                const rangeMin = 0

                // 
                this.translateY += move

                if (this.translateY < rangeMax) {
                    this.translateY = rangeMax
                }
                if (this.translateY > rangeMin) {
                    this.translateY = rangeMin
                }

                this.percent = this.translateY / rangeMax

                this.currentY = event.targetTouches[0].pageY
            },
            onTouchEnd(event) {
                this.$emit("end", event)
                console.log(`%c : ${this.depth} `, "color: #e91e63")

                this.$toast({
                    position: 'center',
                    type: 'loading',
                    message: ' ~',
                    duration: 0
                })

                window.$depth = this.depth

                this.getMapExtent().then(res => {
                    const arr = res.split(',')
                    const params = {
                        dpv: this.depth,
                        minx: arr[0],
                        miny: arr[2],
                        maxx: arr[1],
                        maxy: arr[3]
                    }

                    getDepth(params).then(res => {
                        this.setMapDepth(res)

                        window.Arcgis && window.Arcgis.setAirworthiness([res])

                        this.$toast.clear()
                    }).catch(err => {
                        this.$toast('')
                    })
                })

                setMapEnable(true)
            }
        }
    };
</script>

<style lang="stylus" scoped>
    @import '../../../common/stylus/variable.styl';

    .map-depth
        position absolute
        left 20px
        bottom 160px
        width 32px
        > .depth
            width 100%
            height 100%
        .map-depth-group
            position absolute
            right -52px
            bottom calc(1% - 11px)
            display flex
            flex-direction column
            align-items center
            justify-content center
            padding 2px 8px
            width auto
            min-width 30px
            height 22px
            color #fff
            background-color #298DCD
            border-radius 2px
            overflow visible
            &::before
                content ""
                position absolute
                width 10px
                height 10px
                background-color #298DCD
                left -2px
                transform rotate(45deg)
            &::after
                content ""
                position absolute
                width 8px
                height 8px
                border 1px solid #fff
                border-radius 50%
                background-color #298DCD
                left -13px
            .map-depth-value
                width 30px
                height 15px
                line-height 15px
                text-align center
                font-size $font-size-ss
</style>



