<template>
    <div class="map-compass-button" @click.stop="toggleAngle(angle)">
        <img :style="{transform: `rotate(${degree}deg)`}" src="../../../../static/icon/map_compass.png">
    </div>
</template>

<script>
    import {mapMixins} from "../../../common/mixins"
    import {setMapTruenorth} from "../../../common/js/cordova/map"

    export default {
        name: "map-compass-button",
        mixins: [mapMixins],
        data() {
            return {
                degree: 0
            }
        },
        created() {
            window.$types.map.compass.setAngle = this.setDegree
        },
        methods: {
            setDegree(degree) {
                degree = Number(degree)

                if (typeof degree === 'number' && !window.isNaN(degree)) {
                    this.degree = degree
                }
            },
            toggleAngle(value) {
                if (value === this.ANGLE_NORTH.value) {
                    this.$emit('click')
                }
                if (value === this.ANGLE_HEAD.value) {
                    setMapTruenorth().then(res => {
                        this.setAngle(this.ANGLE_NORTH.value)

                        console.log(`%c ${this.ANGLE_NORTH.value}`, 'color: #673ab7')
                    })
                }
            },
        }
    }
</script>

<style lang="stylus" scoped>

    .map-compass-button
        border-radius 50%
</style>



