<template>
    <mu-ripple :style="active" @click="handlerSelection">
        <img class="icon" :src="icon">
    </mu-ripple>
</template>

<script>
    import {mapGetters} from 'vuex'

    export default {
        name: "map-pin-button",
        props: {
            selection: {
                type: Boolean,
                default: false
            },
            icon1: {
                type: String,
                default: 'static/icon/map_label.png'
            },
            icon2: {
                type: String,
                default: 'static/icon/search_place_name.png'
            }
        },
        computed: {
            active() {
                return this.selection ? 'background-color: #2196f3' : ''
            },
            icon() {
                return this.selection ? this.icon2 : this.icon1
            },
            ...mapGetters({
                novice: 'novice/novice'
            })
        },
        methods: {
            handlerSelection() {
                if (this.novice === 0) return

                this.$emit('click')
            }
        }
    }
</script>

<style lang="stylus" scoped>
</style>



