<template>
    <transition name="fade-drawer">
        <div class="drawer" v-show="visible"
             :class="{'active-left': !right, 'active-right': right}"
             :style="{
                width: width,
                backgroundColor: backgroundColor
              }">
            <slot></slot>
        </div>
    </transition>
</template>

<script>
    export default {
        name: "drawer",
        data() {
            return {
                visible: false,
                width: '200px',
                right: false,
                onClose: null,
                backgroundColor: '#fff'
            }
        },
        methods: {
            destroyElement() {
                this.$el.removeEventListener('transitionend', this.destroyElement)
                this.$destroy(true)
                this.$el.parentNode.removeChild(this.$el)
            },
            close() {
                this.visible = false
            },
            onClick() {
                this.$emit('click', this.$el)
                this.close()
            }
        },
        watch: {
            visible(value) {
                if (!value) {
                    if (typeof this.onClose === 'function') {
                        this.onClose(this)
                    }

                    this.$el.addEventListener('transitionend', this.destroyElement)
                }
            }
        }
    }
</script>

<style lang="stylus" scoped>
    .drawer
        position fixed
        top 0
        bottom 0
        &.active-left
            left 0
        &.active-right
            right 0
</style>



