<template>
    <div class="element-panel border-1px-t shadow">
        <div>
            <span class="name">
                <span v-if="elementData.type">{{elementData.type}}</span>
                <span>{{elementData.name}}</span>
                <tag v-if="elementData.typeId === '001001'"
                     :color="elementData.shared  ? '#ff9800':'#2196f3'"
                     mark>
                    {{elementData.shared  ? '':''}}
                </tag>
            </span>
            <span>: <span class="distance">
                {{elementData.distance | formatDistance}}
                {{elementData.distance > 1000 ? '' : ''}}
            </span></span>
        </div>
        <div>
            <div class="info">
                <!--  -->
                <template v-if="elementData.typeId === '001001'">
                    <p>: {{elementData.createTime}}</p>
                </template>
                <!--  -->
                <!--  -->
                <template v-if="elementData.typeId === '013001'">
                    <p>MMSI: {{elementData.mmsi}}</p>
                    <p>: {{elementData.updateTime}}</p>
                </template>
                <!--  -->
                <!--  -->
                <template v-if="elementData.typeId === '013007'">
                    <p>{{elementData.lightCharacter}}</p>
                </template>
                <!--  -->
                <!--  -->
                <template v-if="elementData.typeId === '013010'">
                    <p>(){{elementData.waterLevel}}</p>
                    <p>{{elementData.reportTime}}</p>
                </template>
                <!--  -->
                <!--  -->
                <template v-if="elementData.typeId === '013011'">
                    <p>{{elementData.temperature}}</p>
                    <p>: {{elementData.windDirection}}</p>
                </template>
                <!--  -->
            </div>
            <div class="button-wrapper">
                <mu-button class="button" color="#fff" @click="$emit('route', elementData)" flat></mu-button>
            </div>
        </div>
    </div>
</template>

<script>
    import {elementMixins} from "../../common/mixins"
    import Tag from 'vant/lib/tab'
    import 'vant/lib/tab/style'

    export default {
        name: "element-panel",
        mixins: [elementMixins],
        components: {
            Tag
        }
    }
</script>

<style lang="stylus" scoped>
    @import "../../common/stylus/variable.styl"

    .element-panel
        padding 20px 26px
        width 100%
        height 110px
        background-color rgba(255, 255, 255, .9)
        > div
            display flex
            align-items center
            margin-bottom 10px
            width 100%
            &:last-child
                margin-bottom 0
            p
                margin-bottom 5px
                font-size 14px
                color #333
                &:last-child
                    margin-bottom 0
        .name
            flex 1
            color #2e7bff
        .distance
            color #2e7bff
        .info
            flex 1
            margin-right 10px
        .button-wrapper
            .button
                background-color #2e7bff
                border-radius 100px
</style>



