<template>
    <mu-ripple class="map-layer-button" @click="onClick">
        <img src="../../../../static/icon/map_layer.png">
        <mu-drawer :open.sync="visible" width="200px"
                   :docked="false"
                   v-transfer-dom
                   right>
            <mu-list dense>
                <mu-sub-header></mu-sub-header>
                <div class="map-mode">
                    <div class="map-mode-item" :class="{active: landMap}"
                         @click="toggleLandMap(!landMap)">
                        <div class="map-mode-icon border-1px">
                            <img :src="LAND_MAP.icon">
                        </div>
                        <span>{{LAND_MAP.name}}</span>
                    </div>
                    <div class="map-mode-item" :class="{active: channelMap}"
                         @click="toggleChannelMap(!channelMap)">
                        <div class="map-mode-icon border-1px">
                            <img :src="CHANNEL_MAP.icon">
                        </div>
                        <span>{{CHANNEL_MAP.name}}</span>
                    </div>
                </div>
                <mu-sub-header></mu-sub-header>
                <div class="map-layers">
                    <div class="map-layers-item" v-for="(item, index) in layers" @click="toggleLayer(item, index)">
                        <mu-avatar class="map-layers-item-icon"
                                   :color="item.status ? item.color : '#D9D9D9'"
                                   size="45">
                            <img :src="item.icon">
                        </mu-avatar>
                        <span>{{item.name}}</span>
                    </div>
                </div>
                <mu-sub-header></mu-sub-header>
                <mu-list-item>
                    <mu-list-item-action>
                        <img class="icon navigation-icon" :src="themeIcon">
                    </mu-list-item-action>
                    <mu-list-item-title></mu-list-item-title>
                    <mu-list-item-action>
                        <div class="txt-switch">
                            <div class="txt-switch-item"
                                 :class="{'active': theme === THEME_LIGHT.value}"
                                 @click="toggleTheme(THEME_LIGHT.value)">
                                {{THEME_LIGHT.name}}
                            </div>
                            <div class="txt-switch-item"
                                 :class="{'active': theme === THEME_NIGHT.value}"
                                 @click="toggleTheme(THEME_NIGHT.value)">
                                {{THEME_NIGHT.name}}
                            </div>
                        </div>
                    </mu-list-item-action>
                </mu-list-item>
                <mu-list-item>
                    <mu-list-item-action>
                        <img class="icon navigation-icon direction" :src="angleIcon">
                    </mu-list-item-action>
                    <mu-list-item-title></mu-list-item-title>
                    <mu-list-item-action>
                        <div class="txt-switch">
                            <div class="txt-switch-item l"
                                 :class="{'active': angle === ANGLE_NORTH.value}"
                                 @click="toggleAngle(ANGLE_NORTH.value)">
                                {{ANGLE_NORTH.name}}
                            </div>
                            <div class="txt-switch-item r"
                                 :class="{'active': angle === ANGLE_HEAD.value}"
                                 @click="toggleAngle(ANGLE_HEAD.value)">
                                {{ANGLE_HEAD.name}}
                            </div>
                        </div>
                    </mu-list-item-action>
                </mu-list-item>
                <mu-sub-header></mu-sub-header>
                <mu-list-item>
                    <mu-list-item-action>
                        <img class="icon depth-icon"
                             :class="{focus: !depth}"
                             src="../../../../static/icon/map_water.png">
                    </mu-list-item-action>
                    <mu-list-item-title></mu-list-item-title>
                    <mu-list-item-action>
                        <mu-switch :input-value="depth" @change="toggleDepth"/>
                    </mu-list-item-action>
                </mu-list-item>
            </mu-list>
        </mu-drawer>
    </mu-ripple>
</template>

<script>
    import Drawer from '../../../base/drawer'
    import MSwitch from '../../../components/switch/switch'
    import {appMixins, mapMixins, layerMixins} from "../../../common/mixins"
    import {
        changeTheme,
        setMapTruenorth,
        setMapCompassMode,
        changeTieldLayerVisible,
        changeChartLayerVisible,
    } from "../../../common/js/cordova/map"
    import Toast from 'muse-ui-toast'


    export default {
        name: "map-layer-button",
        mixins: [appMixins, mapMixins, layerMixins],
        data() {
            return {
                visible: false
            }
        },
        props: {
            disabled: {
                type: Boolean,
                default: false
            },
        },
        methods: {
            onClick() {
                if (this.disabled) return

                this.visible = true
            },
            // 
            toggleLandMap(visible) {
                changeTieldLayerVisible(visible).then(res => {
                    this.setLandMap(visible)

                    console.log(`%c    Visible: ${visible}`, 'color: #111')
                })
            },
            // 
            toggleChannelMap(visible) {
                changeChartLayerVisible(visible).then(res => {
                    this.setChannelMap(visible)


                    console.log(`%c    Visible: ${visible}`, 'color: #111')
                })
            },
            // 
            toggleLayer(item, index) {
                if (index === 1 && !item.status) {
                    Toast.message({
                        position: 'bottom',
                        message: '',
                        time: 5000
                    })
                }

                if (index === 3 && !item.status) {
                    Toast.message({
                        position: 'bottom',
                        message: '',
                        time: 5000
                    })
                }

                item = Object.assign({}, item, {status: !item.status})

                this.saveLayers(item)
            },
            /***
             * 
             * @param theme
             */
            toggleTheme(theme) {
                this.setTheme(theme)

                console.log(`%c ${theme}`, 'color: #673ab7')
            },
            /***
             * 
             * @param value
             */
            toggleAngle(value) {
                if (value === this.ANGLE_NORTH.value) {
                    setMapTruenorth().then(res => {
                        this.setAngle(value)

                        console.log(`%c ${value}`, 'color: #673ab7')
                    })
                }
                if (value === this.ANGLE_HEAD.value) {
                    setMapCompassMode().then(res => {
                        this.setAngle(value)

                        console.log(`%c ${value}`, 'color: #673ab7')
                    })
                }
            },
            toggleDepth(visible) {
                if (visible) {
                    this.setDepth(visible)

                    this.visible = false
                } else {
                    window.Arcgis && window.Arcgis.clearAirworthiness(null, event => {
                        this.setDepth(visible)
                    })
                }
            }
        },
        components: {
            Drawer,
            MSwitch
        }
    }
</script>

<style lang="stylus" scoped>
    @import "../../../common/stylus/variable.styl"

    .mu-list-dense .mu-item
        padding 0 !important

    .mu-sub-header
        line-height 34px
        margin 8px 0
        background #f5f5f5
        color #333

    .mu-paper
        .map-mode
            display flex
            padding 0 10px
            .map-mode-item
                padding 0 5px
                width 50%
                text-align center
                .map-mode-icon
                    position relative
                    margin-bottom 5px
                    width 100%
                    > img
                        display block
                        width 100%
                        height 100%
                > span
                    font-size 13px
                &.active
                    .map-mode-icon
                        border-color #2196f3
                    .map-mode-icon:after
                        content 'done'
                        position absolute
                        left 0
                        bottom 0
                        width 100%
                        height 18px
                        font-family 'Material Icons'
                        text-align center
                        color #fff
                        background-color #2196f3

        .mu-item-action
            min-width 32px
        .icon
            display block
            padding 3px
            width 22px
            height 22px
            border-radius 50%
        .map-layers
            width 100%
            display flex
            flex-wrap wrap
            .map-layers-item
                display flex
                flex-direction column
                align-items center
                padding 7px 0
                width 33.33333%
                .map-layers-item-icon
                    margin-bottom 5px
                    img
                        padding 8px
                span
                    font-size 14px
                    color #333

        .navigation-icon
            background-color #ff5252

        .navigation-icon.direction
            background-color #2196f3

        .depth-icon
            background-color #8BBE4E
            &.focus
                background-color #d9d9d9

        .txt-switch
            display flex
            align-items center
            justify-content center
            padding 0
            width 200px
            height 20px
            border-radius 100px
            box-shadow 0 0 1px #2BC662
            overflow hidden
            .txt-switch-item
                width 50%
                height 25px
                line-height 25px
                color #2BC662
                text-align center
                font-size $font-size-sm
                &.active
                    background-color #2BC662
                    color #fff

</style>



