import Vue from 'vue'
import DrawerComponent from './drawer'
import Shelter from '../shelter'

const DrawerController = Vue.extend(DrawerComponent)

let instance

const Drawer = function (options = {}, component) {
    if (Vue.prototype.$isServer) return

    if (instance) return instance

    let userOnClose = options.onClose

    let id = 'drawer_component'

    options.onClose = function () {
        Drawer.close(userOnClose)

        instance = undefined
    }

    instance = new DrawerController({
        data: options,
        slots: {
            default: component
        }
    })

    instance.id = id

    instance.vm = instance.$mount()

    document.body.appendChild(instance.vm.$el)

    instance.vm.visible = true

    instance.dom = instance.vm.$el

    instance.dom.style.zIndex = 1300

    Shelter.show({
        onClose() {
            Drawer.close()
        }
    })

    return instance.vm
}

Drawer.show = (options, component) => {
    return Drawer(options, component)
}

Drawer.close = userOnClose => {
    instance.visible = false

    typeof userOnClose === 'function' && userOnClose()
}

export default Drawer



