<template>
    <mu-ripple class="map-message-button" @click="handlerToMessage">
        <img :src="icon">
    </mu-ripple>
</template>

<script>
    import {userMixins} from "../../../common/mixins"
    import {mapGetters} from 'vuex'

    export default {
        name: "map-message-button",
        mixins: [userMixins],
        data() {
            return {
                icon: 'static/icon/map_message.png'
            }
        },
        computed: {
            ...mapGetters({
                novice: 'novice/novice'
            })
        },
        methods: {
            handlerToMessage() {
                if (this.novice === 1) return

                if (this.isLogin) {
                    this.$router.push('/about/message?target=notice')
                } else {
                    this.$toast('')
                }
            }
        }
    }
</script>

<style lang="stylus" scoped>
    .map-message-button
        border-radius 50%
</style>



